<template>
    <div id="user" :style="{background:skinColor}">
       <section class="info">
           <span class="infoTitle"></span>
           <span class="infoContent">{{user.username || ''}} </span>
       </section>

       <section class="info">
           <span class="infoTitle"></span>
           <span class="infoContent">{{resume.profile.name || ''}} </span>
       </section>
       
       <section class="info">
           <span class="infoTitle"></span>
           <span class="infoContent">{{user.email || '' }}</span>
       </section>

       <section class="info" v-if="user.email">
           <span class="infoTitle"></span>
           <span class="infoContent">
               <button class="button" @click="showResetPassword"></button>
           </span>
       </section>

       <section class="info">
           <span class="infoTitle"></span>
           <span class="infoContent">
              <!-- <button class="button" @click="uploadAvatar(selected, busEvent)"></button>-->
               <Buttons class="uploadAvatar" @click="uploadAvatar(selected, busEvent)" text=""></Buttons>
               <img :src="userAvatarSrc" class="userAvatarSrc">
               <UploadAvatar :visible="uploadAvatarvisible" @close="closeAvatar" :parent="selected"></UploadAvatar>
           </span>
       </section>

       <section class="info">
           <span class="infoTitle">/</span>
           <span class="infoContent">
               <a :href="userSite" target="new" style="margin: 10px 0; cursor: pointer">{{userSite || ''}}</a>
               <!--<input type="text"  :value="userSite" @input="changeUserSite('userSite',$event.target.value)">-->
           </span>
       </section>

       <section class="info">
           <span class="infoTitle"></span>
           <span class="infoContent contacts" v-for="(contact, index) in resume.contacts">
               <span class="contact">{{contact.contact || ''}}</span>
               <span class="content">{{contact.content || ''}}</span>
           </span>
       </section>


       <section class="info">
           <span class="infoTitle"></span>
           <span class="infoContent">
               <div class="replenish">{{replenish}}</div>
           </span>
       </section>

    </div>
</template>

<script>
    import './user.scss'
    import user from './user.js'
    export default user
</script>


