<template>
    <div>
        <form @submit.prevent="resetPassword(email)">
             <div class="row">
                <label></label>
                <input type="text" v-model="email" required>
            </div>
            <div class="actions">    
                <!--<span class="btn" @click="returnSignIn"></span>-->
                <!--<input type="submit" class="submit" value="">-->
                <Buttons class="btn" @click="returnSignIn" text=""></Buttons>
                <Buttons class="submit" type="submit" text=""></Buttons>
            </div>
            <span class="message">{{message}}</span>
            <span class="email" v-show="isSendSuccess"><a href="javascript:;" target="new"  @click="skip($event.target, email)">{{email}}</a></span>
        </form>
    </div>
</template>

<script>
    import '../../assets/dialog.css'
    import './resetPassword.scss'
    import resetPassword from './resetPassword.js'
    export default resetPassword
</script>


