<template>
    <div>
        <form @submit.prevent="signIn">
            <div class="row">
                <label>/</label>
                <input type="text" v-model="formData.username" required>
            </div>
            <div class="row">
                <label></label>
                <input type="password" v-model="formData.password" required>
            </div> 
            <div class="actions">
                <!--<span class="resetPassword btn" v-show="showVerify(errorMessage)" @click="verifyEmail"></span>-->
               <!-- <span class="resetPassword btn" v-show="showRest(errorMessage)" @click="resetPassword"></span>  -->     
               <!-- <input type="submit" class="submit" value="">-->
                <Buttons class="btn" v-show="showVerify(errorMessage)" @click="verifyEmail" text=""></Buttons>
                <Buttons class="btn" v-show="showRest(errorMessage)" @click="resetPassword" text=""></Buttons>
                <Buttons class="submit" type="submit" text=""></Buttons>
            </div>    
            <span class="errorMessage">{{errorMessage}}</span>
        </form>
    </div>
</template>

<script>
    import '../../assets/dialog.css'
    import './SignInForm.scss'
    import SignInForm from './SignInForm.js'
    export default SignInForm 
</script>



